.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  display: flex ;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;

}

.App-link {
  color: #61dafb;
}

.nav-bar{
	display: flex;
	justify-content: center;
	font-size: 11px;
	padding-bottom: 13px;
}

.nav-links{
	text-decoration: none;
	color: #202020;
	padding-right: 13px;
}

.banner{
	width: 1200px;
	height:200px;
	padding-bottom:20px;
}

.product-box{
	width: 300px;
	border: 1px solid #D8D8D8;
	display: flex;
	flex-direction: row;
}

.product-info{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.items-div{
	display: flex;
	padding-left: 25px;
	padding-right: 25px;
	align-items: flex-start;
}

.filters-div{
	align-items: flex-start;
	padding-right: 90px;
	padding-left: 30px;
	text-align: start;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
